.App {
    display: flex;
    flex-direction: column;
}

.appHeader {
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 1px 2px 2px #00000033;
}

.soppingCartIcon {
    font-size: 30px;
    margin-right: 16px;
    cursor: pointer;
}

.productsContainer {
    padding: 8px;
}

.appMenu {
    border: none;
}

.pageContent {
    /* display: flex; */
    flex: 1;
    overflow-y: auto;
    margin-top: 2px;
    min-height: 469px;
}

.appFooter {
    height: 60px;
    background-color: black;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.itemCard,
.itemCardBadge {
    margin: 8px;
}

.itemCardImage {
    height: 150px !important;
    object-fit: scale-down;
}